.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 414px;
  width: 100%;
  height: 100%;
  background-color: $color-primary-bg;
  border-radius: 2px;
  padding-top: 60px;
}

.form-table {
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;

    text-transform: uppercase;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: calc(28px / 24px);
    letter-spacing: 0.05em;
    color: $color-primary-text;
  }

  &__box {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__input {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__field {
    padding-left: 12px;
    padding-right: 12px;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: calc(23px / 20px);
    letter-spacing: 0.05em;
    color: #808080;
  }

  &__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: calc(17px\14px);
    letter-spacing: 0.02em;
    color: #c79002;
  }

  &__button {
    max-width: 384px;
    height: 64px;
    text-transform: uppercase;
    border: 1px;
    background-color: $color-button;
    color: $color-button-text;
    cursor: pointer;
  }
}

.close-button {
  width: 24px;
  height: 24px;
  top: 16px;
  right: 1px;
  position: absolute;
  border: 0;
  padding: 0;
  background-color: inherit;
  cursor: pointer;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-duration: 1.5s;
}

.modal1.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.modal2.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.modal3.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 41;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  overflow: scroll;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}
body.modal-open {
  overflow: hidden;
}
